/*sticky footer styles*/
/*https://v4-alpha.getbootstrap.com/examples/sticky-footer/sticky-footer.css*/
html {
  position: relative;
  min-height: 100%;
}
body {
  /* Margin bottom by footer height */
  margin-bottom: 60px;
}
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  height: 60px;
  line-height: 60px; /* Vertically center the text there */
  text-align: center;
}
/*sticky footer styles*/

html, body {
  background: #151515;
  font-size: 1.0025em;
}

.linkBox {
	border-style: groove;
	border-width: 5px;
	border-radius: 25px;
	border-color: grey;
  padding: 15px;
}

.contactLinks {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	vertical-align: middle;
}

.contactLinkImg {
	border-radius: 50%;
	margin: 2px;
}

.contactLinkImg:hover {
	-webkit-box-shadow: 0 0 3pt 2pt grey;
	        box-shadow: 0 0 3pt 2pt grey;
}

.err404 {
  font-family: "Impact, Charcoal, sans-serif";
}
.err404 > h1 {
  font-weight: bold;
}

.addPadding {
  padding-bottom: 10px;
}

.linkList {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}


